import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { MetadataService, ITool } from '../metadata.service';
import { PageService } from '../page.service';

export class Transform<T> {
  input: string;
  options: T;
}

@Component({
  selector: 'app-transform-tool',
  templateUrl: './transform-tool.component.html',
  styleUrls: ['./transform-tool.component.scss']
})
export abstract class TransformToolComponent implements OnInit {

  abstract id: string;
  abstract action: string;
  abstract onTransform(transform: any): string;
  abstract documentation: string;

  abstract schema: any;
  layout: any;
  formOptions = {}
  data: any = {};
  result: string = null;
  language: string;
  private metadata: ITool

  constructor(private service: MetadataService, private page: PageService) {
  }

  ngOnInit() {
    this.metadata = this.service.getTool(this.id);
    this.page.set({
      title: this.metadata.title,
      description: this.metadata.description
    });
    const outputFormat = this.metadata.tags
      .map(t => t.toLowerCase())
      .filter(t => t.startsWith('output:'))[0]
      .replace('output:', '')
      .replace(' ', '-');
    this.layout = [
      '*',
      {
        type: 'submit',
        title: this.action
      }
    ];
    this.language = this.getLanguage(outputFormat);
  }

  private getLanguage(format: string) {
    switch (format) {
      case 'json': return 'javascript';
      case 'json-schema': return 'javascript';
      default: return format;
    }
  }

  onSubmit() {
    this.result = this.onTransform(this.data);
  }
}
